<template>
  <div class="afterSaleApply">
<!--    <van-nav-bar title="售后申请" :border="false" left-arrow @click-left="goBack">-->
<!--      <div slot="right" class="submit" @click="clickRight">提交</div>-->
<!--    </van-nav-bar>-->
    <navigation-bar  @onLeftClick="goBack" pageName="售后申请" >
      <template v-slot:nav-right>
        <div class="submit" @click="clickRight">提交</div>
      </template>
    </navigation-bar>
    <div class="content" v-if="afterSaleDetail">
      <div class="box">
        <div class="top">
          <div class="shop_info">
            <div class="shop_name">{{afterSaleDetail.shopName}}</div>
            <div class="status">已发货</div>
          </div>
          <div class="order_info">
            <div class="left">
              <img :src="afterSaleDetail.spuPicUrl" alt="">
            </div>
            <div class="right">
              <div class="good_name">{{afterSaleDetail.spuName}}</div>
              <div class="good_unit">
                <div class="unit">{{afterSaleDetail.attrValueContent}}</div>
                <div class="num">×{{afterSaleDetail.skuCount}}</div>
              </div>
            </div>
          </div>
        </div>
        <div class="middle">
          <div class="left">退款金额</div>
          <div class="right"><span>￥</span><span>{{afterSaleDetail.returnRealTotal}}</span></div>
        </div>
        <div class="bottom">
          <div class="reason" @focusout="inputFocus">
            <textarea name="" id="" placeholder="请输入退货原因" v-model="reason" maxlength="100"></textarea>
          </div>
          <div class="photo_box">
            <div class="title">
              <span class="add">添加照片</span>
              <span>（最多4张）</span>
            </div>
            <div class="line"></div>
            <div class="image-box">
<!--              <add-image v-on:imageAddAction="showAddSheetControll" v-on:imageMapChange="upDateImageData" v-on:changeUploadStatus="changeStatus" :is-upload="isUpload" ref="addImageComponent"></add-image>-->
              <van-uploader v-model="vantFileList" preview-size="50" :after-read="afterRead" :max-count="4" accept="image" :before-delete="deletePic"></van-uploader>
            </div>
          </div>
        </div>
      </div>
    </div>
    <van-action-sheet
          v-model="showAddSheet"
          :actions="actions"
          cancel-text="取消"
          :round="false"
          close-on-click-action
          @select="addSelect"/>
  </div>
</template>

<script>
import { applyForAfterSale } from "../../services/orderListService";
import AddImageVue from "../../components/addImage/AddImage.vue";
import NavigationBar from '@/common/currency/NavigationBar.vue';
import {uploadFileService} from "../../services/commonPhoneService";
export default {
  name: "afterSaleApply",
  data() {
    return {
      reason: "",
      imgList: [],
      vantFileList:[],
      afterSaleDetail: null,
      showAddSheet: false,
      actions: [{ name: "拍照" }, { name: "从手机相册选择" }],
      isUpload: false,
      /*大字模式*/
      bindThemeL: this.$curTheme == 0 ? false : true,
    };
  },
  created() {
    this.afterSaleDetail = this.$route.params.detail;
    this.bindThemeL = this.$curTheme == 0 ? false : true;
      if (this.$deciceType=='phone'){
          require('../../assets/css/phone/myOrderList/afterSaleApply.scss')
      }else if (this.$deciceType=='portraitPad'){
          require('../../assets/css/portraitPad/myOrderList/afterSaleApply.scss')
      }
    // = {
    //   attrValueContent: "器型:9寸品锅+汤勺",
    //   billNo: "347273593601122304C3",
    //   billStatus: "1",
    //   contactPhone: "18561566407",
    //   createdTime: "2019-10-24 17:08:07",
    //   returnCount: 1,
    //   returnPicUrls: [],
    //   returnRealTotal: 0.31,
    //   returnReason: "退货",
    //   saleOrderBillNo: "347273221339865088C1",
    //   saleOrderSubBillNo: "347273308967264256C2",
    //   shopCode: "319019907922763776",
    //   shopName: "网易严选",
    //   skuCount: 1,
    //   skuNo: "342944290763964416",
    //   skuPrice: 169,
    //   spuCode: "342944290717827072",
    //   spuName: "金玉满堂骨瓷餐具4人装 16头9寸品锅+汤勺",
    //   spuPicUrl: "https://yanxuan-item.nosdn.127.net/2ca85f1c86455c9f1e024df117169750.png",
    //   updatedTime: "2019-10-24 17:17:21"
    // }
  },
  components: {
    "add-image": AddImageVue,
    NavigationBar
  },
  methods: {
      inputFocus(e) {
          let UA = window.navigator.userAgent.toLowerCase();
          if (UA.indexOf('iphone') > -1) {
              if (e && e.target && e.target.tagName) {
                  // 输入框失去焦点，要把IOS键盘推出页面的滚动部分还原。即将页面滚动到视窗顶部对齐
                  this.blurTimer =setTimeout(() => {
                      window.scrollTo(0,0);
                  }, 0)
              }
          }
      },
    afterRead(file){
      console.log(this.vantFileList);
        this.$forceUpdate();
        let Base64 = require('js-base64').Base64;
        let params = {
          "baseString":Base64.encode(file.content),
          "source":"yzApp",
          "business":"dicoveryPic",
        };
        uploadFileService(params)
                .then(res => {
                  this.imgList.push(res.data.urls[0]);
                })
                .catch(function (error) {
                  // this.$toast(error);
                  console.log("不对劲 "+error);
                });
    },
    deletePic(file,detail){
      this.imgList.splice(detail.index,1);
        this.$forceUpdate();
        return true;
    },
    /**
     * 页面返回事件
     */
    goBack() {
      if (this.$store.state.preview.previewId) {
        this.$store.dispatch("setPreviewId", "");
      }
      this.$router.go(-1);
    },

    /**
     * 提交售后申请
     */
    submit() {
      var data = {
        skuNo: this.afterSaleDetail.skuNo,
        billNo: this.afterSaleDetail.saleOrderBillNo,
        subBillNo: this.afterSaleDetail.saleOrderSubBillNo,
        shopCode: this.afterSaleDetail.shopCode,
        shopName: this.afterSaleDetail.shopName,
        ownerCode: this.$userInfo.OWNER_CODE, // "2018122909483465412",
        ownerName: this.$userInfo.OWNER_NAME, // "5460_yz",
        areaCode: this.$userInfo.AREA_CODE, // "2018012300015",
        spuCode: this.afterSaleDetail.spuCode,
        spuName: this.afterSaleDetail.spuName,
        attrValueContent: this.afterSaleDetail.attrValueContent,
        skuPrice: this.afterSaleDetail.skuPrice,
        returnReason: this.reason,
        returnCount: this.afterSaleDetail.skuCount,
        skuPicUrl: this.afterSaleDetail.picUrl,
        returnTotal: this.afterSaleDetail.returnRealTotal,
        returnPicUrlList: this.imgList,
        returnAddr: ""
      };
      applyForAfterSale(data)
        .then(res => {
          if (res.resultCode == "0") {
            this.$toast("申请已提交");
            this.goBack();
          } else {
            this.$toast(res.msg);
          }
        })
        .catch(function(error) {
          this.$toast(error);
        });
    },

    clickRight() {
      if (this.reason.length == 0) {
        this.$toast("退货原因不能为空");
        return;
      } else {
        // this.isUpload = true;
        this.submit();
      }
    },

    changeStatus() {
      this.isUpload = false;
    },

    /**
     * 子组件更新已选图片集合监听函数
     **/
    upDateImageData(newImageData) {
      this.imgList = newImageData;
      this.submit();
    },

    /**
     * addImage组件添加照片时的监听函数父组件用来选择哪种图片获取方式(拍照或相册上传)
     **/
    showAddSheetControll() {
      this.showAddSheet = !this.showAddSheet;
    },

    /**
     * 选择当前图片来源方式(拍照或本地上传)
     * @param
     * item      Object   当前选择的方式对象
     * index     Number   当前选择的索引
     **/
    addSelect(item, index) {
      this.getPictureData(index);
    },

    /**
     * 调用摄像头或者本地相册进行图片上传
     * @param
     **/
    getPictureData(selectIndex) {
      selectIndex
        ? this.$refs.addImageComponent.getPicture()
        : this.$refs.addImageComponent.useCamera();
    }
  }
};
</script>

